import cartTypes from "./cart.types";

export const addProduct = (nextCartItem) => ({
    type:  cartTypes.ADD_TO_CART,
    payload: nextCartItem
});

export const removeCartItems = (cartItem) => ({
    type: cartTypes.REMOVE_CART_ITEMS,
    payload : cartItem
});

export const reduceCartItems = (cartItem) => ({
    type:cartTypes.REDUCE_CART_ITEMS,
    payload: cartItem
});

