import React from 'react';
import { useDispatch } from 'react-redux';
import { removeCartItems, addProduct, reduceCartItems } from '../../../redux/Cart/cart.actions';

const Item = ( product ) => {
    const dispatch = useDispatch();
    const {
        productName,
        productThumbnail,
        productPrice,
        quantity,
        documentID
    } = product;

    const handleRemoveCartItem = (documentID) => {
        dispatch(
            removeCartItems({
                documentID
            })
        )
    }

    const handleReduceCartItem = (product) => {
        dispatch(
            reduceCartItems(product)
        )
    }

    const handleAddProduct = (product) => {
        dispatch(
            addProduct(product)
        )
    }

    return (
        <table className="cartItem" border="0"  cellSpacing="0" cellPadding="10">
            <tbody>
                <tr>
                    <td>
                        <img src= {productThumbnail} alt={productName} />
                    </td>
                    <td>
                        {productName}
                    </td>
                    <td>
                        <span className="cartBtn"
                        onClick={() => handleReduceCartItem(product)}>
                            {' < '}
                        </span>
                        <span>
                            {quantity}
                        </span>
                        <span className="cartBtn"
                        onClick={() => handleAddProduct(product)}>
                            {' > '}
                        </span>
                    </td>
                    <td>
                        Rs: {productPrice}
                    </td>
                    <td align="center">
                        <span className="cartBtn" onClick={() => handleRemoveCartItem(documentID)}>
                            Remove Item
                        </span>
                    </td>
                </tr>
            </tbody>
        </table>
    )
}

export default Item;